@font-face {
  font-family: "fontello";
  src: url("../fonts/fontello/fontello.eot?15270993");
  src: url("../fonts/fontello/fontello.eot?15270993#iefix")
      format("embedded-opentype"),
    url("../fonts/fontello/fontello.woff2?15270993") format("woff2"),
    url("../fonts/fontello/fontello.woff?15270993") format("woff"),
    url("../fonts/fontello/fontello.ttf?15270993") format("truetype"),
    url("../fonts/fontello/fontello.svg?15270993#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
